@import url('https://fonts.googleapis.com/css2?family=Cormorant+Garamond:wght@300&display=swap');
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  border-radius: 20px;
  
 
  
}

body {
  font-family: 'Montserrat', sans-serif;


  
}
.Application{
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 30%;
  padding-top: 5%;
  padding-bottom: 50%;
  
  
}  

.app {
  background-image: url('./images/cold.jpg');
  background-size: cover;
  background-position: bottom;
  transition: 0.4 ease;
  box-shadow: 3px 3px rgb(194, 193, 193);
  max-width:350px;
  min-width: 300px;
  
 
}

.app.warm {
  background-image: url('./images/warm.jpg');
  
}

main {
  
  min-height: 80vh;
  background-image: linear-gradient(to bottom, rgba(250, 243, 243, 0.2), rgba(143, 49, 49, 0.75));
  padding: 25px;
  
}

.search-box {
  width: 100%;
  margin: 0 0 75px;
}

.search-box .search-bar {
  display: block;
  width: 100%;
  padding: 15px;

  appearance: none;
  background: none;
  border: none;
  outline: none;

  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 0px 0px 16px 16px;
  margin-top: -25px;

  box-shadow: 0px 5px rgba(0, 0, 0, 0.2);
  
  color: #313131;
  font-size: 20px;

  transition: 0.4s ease;
}

.search-box .search-bar:focus {
  background-color: rgba(255, 255, 255, 0.75);
}

.location-box .location {
  color: #FFF;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}

.location-box .date {
  color: #FFF;
  font-size: 20px;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
}

.weather-box {
  text-align: center;
}

.weather-box .temp {
  position: relative;
  display: inline-block;
  margin: 30px auto;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 16px;
  padding: 15px 25px;
  width: 100%;
  color: white;
  font-size: 70px;
  font-weight: 700;

  text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
  text-align: center;
  box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
}

.weather-box .weather {
  color: white;
  font-size: 48px;
  font-weight: 700;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}
h3{
  
  margin: 0 auto;
  padding-bottom: 20px;
  font-family: 'Cormorant Garamond', serif;
  font-size: 50px;
}